.pushBlock {
    .pushGeneral {
        height: 6rem;
        background-color: #646d8d;
        .pushContent {
            display: flex;
            align-items: center;
            height: 6rem;
            padding: 0 2.5rem;

            @media (max-width: 425px) {
                flex-direction: column;
                justify-content: center;
                align-items: inherit;
            }
        }
    }
}