.l {
    height: 20rem;
    width: 20rem;

    @media(max-width: 1000px) {
        height: 15rem;
        width: 15rem;
    }
}

.m {
    height: 6.5rem;
    width: 6.5rem;

    @media(max-width: 370px) {
        height: 5rem;
        width: 5rem;
    }
}

.s {
    height: 5rem;
    width: 5rem;
}

.img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    image-rendering: -webkit-optimize-contrast;
}